import {Component, ViewChild, ViewContainerRef} from '@angular/core';
import {Context, DynaContent} from '@er/types';
import {FieldWrapper} from '@ngx-formly/core';

export interface DynamicProps {
  before?: boolean;
  after?: boolean;
  content?: DynaContent;
  context?: Context;
}

@Component({
  template: `
    <ng-container *ngIf="to['before']">
      <ng-container *ngTemplateOutlet="content"></ng-container>
    </ng-container>
    <ng-container #fieldComponent></ng-container>
    <ng-container *ngIf="to['after']">
      <ng-container *ngTemplateOutlet="content"></ng-container>
    </ng-container>
    <ng-template #content>
      <er-dyna-content [content]="to['content']"
                       [context]="to['context']"
                       [styleClass]="to['styleClass']"
                       [style]="to['style']"
      ></er-dyna-content>
    </ng-template>
  `
})
export class DynamicWrapperComponent extends FieldWrapper {
  @ViewChild('fieldComponent', {read: ViewContainerRef}) fieldComponent: ViewContainerRef;
}
