import {Directive, OnInit} from '@angular/core';
import {PropsBindDirective} from '@er/core';

@Directive({selector: '[formlyPropsBind]'})
export class FormlyPropsBindDirective extends PropsBindDirective implements OnInit {

  get field() {
    const viewComponent = this.viewComponentRef;
    if (viewComponent) {
      return viewComponent.field;
    }
  }

  ngOnInit(): void {
    if (this.hostComponentInst) {
      this.hostComponentInst['ctx'] = {
        ...this.hostComponentInst['ctx'] || {},
        field: this.field
      };
      this.hostComponentInst['formlyField'] = this.field;
    }
  }
}
