import {ChangeDetectorRef, Component, Input} from '@angular/core';
import {BaseFormComponent} from '@er/core';
import {FormProps} from '@er/types';
import {FormlyFormOptions} from '@ngx-formly/core';
import {FormlyValueChangeEvent} from '@ngx-formly/core/lib/components/formly.field.config';
import {Subject} from 'rxjs';
import {ErFormlyFieldConfig} from '../../base/field-config';

@Component({
  selector: 'er-formly-form',
  templateUrl: 'tpl.html'
})
export class FormlyFormComponent extends BaseFormComponent {

  @Input() fields: ErFormlyFieldConfig[];

  @Input() options: FormlyFormOptions = {};

  formProps: FormProps;

  valueChanges$: Subject<FormlyValueChangeEvent> = new Subject();

  constructor(protected cd: ChangeDetectorRef) {
    super(cd);
  }

  get formlyOptions(): FormlyFormOptions {
    return {
      fieldChanges: this.valueChanges$,
      ...this.options
    };
  }

  get submitTips() {
    if (!this.form.valid) {
      const fields = this.getFormErrorFields();
      const labels = [];
      fields.forEach(field => {
        const formlyField = this.formlyField(field);
        if (formlyField) {
          const label = formlyField.templateOptions && formlyField.templateOptions.label || field;
          labels.push(label.replace(':', ''));
        }
      });
      return `è¯·æ­£ç¡®åä»¥ä¸ä¿¡æ¯ï¸ï¼${labels.join(',')}`;
    }
  }

  ngOnInit() {
    super.ngOnInit();
    const valueChanges$ = this.options.fieldChanges || this.valueChanges$;
    if (this.valueChanges) {
      this.$subscriptions = valueChanges$.subscribe(event => {
        this.valueChanges({
          ...event,
          formlyForm: this
        });
      });
    }
  }

  getEventContext(other?) {
    return super.getEventContext({
      fields: this.fields,
      formOptions: this.options,
      formProps: this.formProps,
      formlyForm: this
    });
  }

  formlyField(key) {
    const props = this.fields.filter(prop => prop.key === key);
    return (props && props.length === 1) ? props[0] : undefined;
  }

}
